html {
  text-align: center;
  background: black;
  box-sizing: border-box;
}

p {
  color: white;
}

h1 {
  color: white;
}

.save-button {
  margin: 2rem auto;
  background: yellow;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-weight: 700;
}

.circle-div {
  height: 50px;
  width: 50px;
  background: yellow;
  border-radius: 50%;
  margin: 0 auto;
}

.square-div {
  height: 75px;
  width: 75px;
  background:  yellow;
  border-radius: 15px;
  margin: 3rem 20vw;
}

.description-p {
  line-height: 2rem;
}

.about-div {
  display: flex;
  flex-direction: column;
  width: 80vw;
  margin: 0 auto;
  background: rgb(241, 96, 120);
  padding: 1rem;
  border-radius: 1rem;
}

a {
  text-decoration: none;
  color: rgb(149, 224, 236);
}